import { useState, useEffect } from "react";
import { FaMoneyCheckAlt } from "react-icons/fa";
import { GiHealthPotion,GiArtificialIntelligence } from "react-icons/gi";

const OneService = (props) =>{
    const [hover,sethover]=useState(false);
    return(
        <div className="container___sercive_c" onMouseEnter={()=>{sethover(true)}} onMouseLeave={()=>{sethover(false)}} style={{ background:!hover?"#fff":"#0075ff",boxShadow:!hover?" 0 0 2px 1px #837d7d40":"0 0 5px 2px #0075ff99", }} >
            <props.Icon color={hover?"#fff":"#0075ff"} className="icons___sercive_c"/>
            <div className="title___sercive_c" style={{ color:hover?"#fff":"#1a313f" }}>{props.title}</div>
            <div className="desc____sercive_c" style={{ color:hover?"#fff":"#000" }}>{props.description}</div>
        </div>
    )
}

const OurServices = (props) =>{
    const [language,setlanguage]=useState(props.lng);
    const [Languages,setlanguages]=useState(props.lngs);

    useEffect(()=>{setlanguage(props.lng)});

    return(
        <div className="serv___cc" style={{ direction:language==="ar"?"rtl":"ltr"  }}>
            <div  className="header__p1_el1__ti">{Languages[language]["Our_Service"]}</div>
            <br/><br/><br/>
            <div className="cc__le___sercive_c" style={{ textAlign:'center' }} >
                <OneService Icon={GiHealthPotion} title={Languages[language]["Early_d"]} description={Languages[language]["early_d"]}  />
                <OneService Icon={FaMoneyCheckAlt} title={Languages[language]["fServices"]} description={Languages[language]["free_d"]}  />
                <OneService Icon={GiArtificialIntelligence} title={Languages[language]["Ari"]} description={Languages[language]["Ai_d"]}  />
            </div>
            <br/>
            <br/>
        </div>
    )
}
export default OurServices;