import { useState, useEffect } from "react";
import Img1 from './Img1.png';
import img2 from './img2.png';
import img3 from './img3.png';
const Elementi=(props)=>{
    return(
                
                    <div className="why___imgs_cc_el" style={props.style}>
                        <div className="why___imgs_cc_img"> <img src={props.img} /> </div>
                        <div className="why___imgs_cc_text">{props.title} </div>
                    </div>
    )
}
const WhyOurApp = (props) =>{
    const [language,setlanguage]=useState(props.lng);
    const [Languages,setlanguages]=useState(props.lngs);

    useEffect(()=>{setlanguage(props.lng)});

    return(
        <div className="serv___cc" style={{ direction:language==="ar"?"rtl":"ltr"  }}>
            <div  className="header__p1_el1__ti">{Languages[language]["why_choose"]}</div>
            <div className="header__p1_el1__el_c">
                <div className="header__p1_el1_why">
                    <div className="header__p1_el1__d__why"> {Languages[language]["why__1"]}  </div>
                    <div className="header__p1_el1__b">
                    <a  onClick={()=>{document.getElementById('boot').scrollIntoView();}} > {Languages[language]["btnn_h"]}  </a>
                    </div>
                </div>
                <div className="why___imgs_cc">
                    <Elementi img={Img1} title={Languages[language]["wh_text_1"]} style={{ position:"absolute",top:"0" }} />
                    <Elementi img={img2} title={Languages[language]["wh_text_2"]}  style={{ position:"absolute",bottom:"0" }}/>
                    {
                    language==="ar" ?
                        <Elementi img={img3} title={Languages[language]["wh_text_3"]}  style={{ position:"absolute",top:"30%",left:"0" }}/>
                    :
                        <Elementi img={img3} title={Languages[language]["wh_text_3"]}  style={{ position:"absolute",top:"30%",right:"0" }}/>

                    }
                </div>
                
            </div>
            <br/><br/>
        </div>
    )
}
export default WhyOurApp; 