
import { useState, useEffect } from "react";
import logo from './Logo2.svg';
import doctor from './doctor.png';

const Header = (props) =>{
    const [language,setlanguage]=useState(props.lng);
    const [Languages,setlanguages]=useState(props.lngs);

    useEffect(()=>{setlanguage(props.lng)});
 
    return(
        <header style={{ direction:language==="ar"?"rtl":"ltr" }}>
            <div className={"logo___top__h_p"+(language==="ar"?"_ar":"")} >
            <img src={logo}  alt=""  loading="lazy"/>
            <div>{Languages[language]["WebTitle"]} </div>
            </div>
            <div className="container___top__header1">
            <div className="header__p1_el0">
                <div className="header__p1_el1__c0">
                <img src={doctor} alt="" loading="eager" />
                </div>
                <div className="header__p1_el1__c1" />
            </div>
            <div className="header__p1_el1">
                <div className={"header__p1_el1__t"+(language==="ar"?"_ar":"")}>{Languages[language]["Headert1"]} </div>
                <div className="header__p1_el1__ti">{Languages[language]["Headert2"]} </div>
                <div className="header__p1_el1__d"> {Languages[language]["Headerd1"]}  </div>
                <div className="header__p1_el1__b">
                <a  onClick={()=>{document.getElementById('boot').scrollIntoView();}} > {Languages[language]["btnn_h"]}  </a>
                </div>
            </div>
            </div>
      </header>
    );
}
export default Header;