import { useState, useEffect } from "react";
import logo1 from './Logo2.svg';


const Footer = (props) =>{
    const [language,setlanguage]=useState(props.lng);
    const [Languages,setlanguages]=useState(props.lngs);

    useEffect(()=>{setlanguage(props.lng)});
    return(
        <footer style={{ position:'relative',padding:"10px 0" }}>
            <div className="logo___top__h_p">
                <img src={logo1}  alt="" className="__"/>
                <div  className="__">{Languages[language]["WebTitle"]} </div>
            </div>
            <br/>
            <div className="footer__copyriht"> {Languages[language]["fcopyr"]} </div>
        </footer>
    )
}
export default Footer;