const Change_checkbox = (e) =>{
  let v=e.target.value+",";
  if(e.target.checked===true) sym+=v;
  else {sym=sym.replace(v,'');}
}
const IsDone = ({triggerNextStep,language})=>{
  const [stop,setstop]=useState(false);
  const [emty,setempty]=useState(sym==="");
    useEffect(()=>{
      if(stop===false && emty) {
        setstop(true);
        triggerNextStep({trigger:"v-0"});
      }else{
        if(stop===false ) 
          triggerNextStep({trigger:"sendd"});
      }
    },[stop])
  if(emty) return(Languages[language]["pmark"]);
  return(Languages[language]["wamin"])
  
}
const SendData = ({language,triggerNextStep})=>{
  const [send,setsend]=useState(false);
  const [die,setdie]=useState(false);
  const [url,seturl]=useState(false);
  const getData = () =>{
    setsend(true);
  if(url===false)
    fetch("https://pfa.trackiness.com/getURLs")
    .then(response => response.json())
    .then(data => {
      seturl(data);
    });
    if(url!==false)
    fetch(url+"?Symptom="+sym)
    .then(response => response.json())
    .then(data => {
      diese=data;
     if(diese.result!=null && diese.result!="Some thing wrog" ){setdie(diese);score=diese.score;Tim=diese.time;triggerNextStep({trigger:"score"})} 
    })
   // .catch(()=>{setsend(false);})
    // var response = await fetch(url+"?Symptom="+sym);
    // if(!response.ok) setsend(false);
    // diese = await response.json();
    setsend(false);
  }
  const [sendf2,setsendf2]=useState(false);
  useEffect(()=>{
    if(send===false && die===false) getData();
    if(die.result!=null  && sendf2===false){
      setsendf2(true);
      fetch("https://pfa.trackiness.com/api/addSymptomsRecord/"+sym+"/"+die.result).then(r=>{return r.json()}).catch(()=>setsendf2(false))}
  })

  if(die!=false) return (
    <>
    {
      Languages[language]["disease"][die.result]!=null ? 
      <>{Languages[language]["disease_"]+Languages[language]["disease"][die.result]}</>
      :
      Languages[language]["stwrong"]
    }
    </>
  );
  return(<PulseLoader color="#fff" size="5" />)
}

